import logo from './logo.svg';
import './App.css';
import {Typography, Grid, TextField, Box, Button, Paper, LinearProgress, IconButton} from "@mui/material";
import DownloadIcon from '@mui/icons-material/Download';
import {useState, useEffect} from "react";

class DownloadVideoToServer {
    constructor(name, id, onStart, progressUpdate, onCompletion, timeout){
        this.name = name;
        this.id = id;
        this.onStart = onStart;
        this.progressUpdate = progressUpdate;
        this.onCompletion = onCompletion;
        this.DL_URL = 'https://pwdl_b.shubhamakshit.me/create_task';
        this.PRG_URL = "https://pwdl_b.shubhamakshit.me/progress/";
        this.GET_URL = '';
        this.progress = 0.0;
        this.completed = 0;
        this.timeout = timeout;

        //this.startDownload();
    }


    startDownload() {

        const dl_headers = {
            'Content-Type': 'application/json',
            'Access-Control-Allow-Origin': '*',
            "Access-Control-Allow-Headers": "Origin, X-Requested-With, Content-Type, Accept"
        };

        const body = {
            'name': this.name,
            'id': this.id
        };

        fetch(this.DL_URL, {
            method: 'POST',
            headers: dl_headers,
            body: JSON.stringify(body),
        })
            .then(response => response.json())
            .then(
                (data) => {
                    const task_id = data.task_id;
                    if (task_id) {
                        this.onStart(task_id);
                        this.task_id = task_id;
                    }

                    this.interval_id = setInterval(() => this.trackProgress(this.task_id), this.timeout);


                }
            )
            .catch((error) => {
                console.error('Error:', error);
            });

    }

    trackProgress(task_id) {
        const trackHeaders =
            {
                'Content-Type': 'application/json',
                'Access-Control-Allow-Origin': '*'
            };


        fetch
        (
            `${this.PRG_URL}${task_id}`,
            {
                method: 'GET',
                headers: trackHeaders,
            }
        )
            .then(response => response.json())
            .then
            (
                data => {

                    if (data.status === "completed" || data.status === "failed") {
                        this.completed = 1;
                        this.onCompletion(data);
                        clearInterval(this.interval_id);
                    }

                    const progress = data.progress.progress;
                    this.progressUpdate(data);

                }
            )
            .catch(error => console.log(error));
    }
};

function App() {
    const [task_id, setTask_id] = useState('');
    const [done, setDone] = useState(false);
    const [intervalId, setIntervalId] = useState(null);
    const [output, setOutput] = useState('');
    const [progress, setProgress] = useState(0);
    const [downloadUrl, setDownloadUrl] = useState('');

    const startDownload = () => {
        const name = document.getElementById('outlined-basic-name').value;
        const id = document.getElementById('outlined-basic-id').value;

        const onStart = (task_id) => setTask_id(task_id);
        const trackProgress = (data) => {
            setOutput(data.progress.str);
            setProgress(data.progress.progress);
        };
        const handleCompletion = (data) => {
            setDone(true);
            setDownloadUrl(`https://pwdl_b.shubhamakshit.me${data.url}`);
        }

        const dl = new DownloadVideoToServer(name, id, onStart, trackProgress, handleCompletion, 500);

        dl.startDownload();

    };

    return (
        <div>
            <Box sx={{flexGrow: 1}} p={4} id={"box"}>
                <Grid container spacing={2}>
                    <Grid item xs={6}>
                        <TextField fullWidth id="outlined-basic-name" label="Name" variant="outlined"/>
                    </Grid>
                    <Grid item xs={6}>
                        <TextField fullWidth id="outlined-basic-id" label="Id" variant="outlined"/>
                    </Grid>
                    <Grid item xs={12}>
                        <Button variant="contained" onClick={startDownload}>Submit</Button>
                    </Grid>
                    <Grid item xs={12}>
                        <Paper variant="outlined" sx={{p: 2, minHeight: 100}}>
                            <Typography variant="body1" component="div" style={{whiteSpace: 'pre-wrap'}}>
                                {output}
                            </Typography>
                        </Paper>
                    </Grid>
                    <Grid item xs={12}>
                        <LinearProgress variant="determinate" value={progress}/>
                    </Grid>
                    {done && (
                        <Grid item xs={12} style={{textAlign: 'center', marginTop: '10px'}}>
                            <Button variant="contained" endIcon={<DownloadIcon/>} href={downloadUrl} download>Download </Button>
                        </Grid>
                    )}
                </Grid>
            </Box>
        </div>
    );
}

export default App;
